@import 'components/variables';
@import "components/generales/accordion";
@import 'components/generales/banner-dudas.scss';

.section-1 {
  padding-top: 3rem;
  padding-bottom: 3rem;
  .line-material {
    width: 6.5rem;
    height: .6rem;
    background-color: $blue-tabla4;
    opacity: 1;
  }
  .contenido-card {
    padding: 2rem;
    background-color: $white;
    border: 1px solid $gray-siroco;
    border-radius: 15px;
    transition: all .3s;
    &:hover {
      background-color: $blue-claro;
      .title-card-clases {
        color: $white;
      }
      .line-card {
        color: $white;
      }
      .text-card-clases {
        color: $white;
      }
      .btn-blue-white {
        color: $white;
        background: $red-siroco;
        &:hover {
          color: $red-siroco;
        }
        &:before {
          background: $white;
          border-radius: 50px;
        }
      }
    } 
  }
  .title-card-clases {
    font-weight: 600;
    font-size: 2rem;
    color: $blue-claro;
    line-height: 30px;
  }
  .line-card {
    width: 3rem;
    opacity: .8;
    color: $blue-claro;
    margin: 1rem auto 0 auto;
  }
  .text-card-clases {
    color: $gray-siroco;
  }
  .table {
    color: $white;
    overflow-y: hidden;
    @media (max-width: 424.98px) {
      font-size: .8rem;
    }
  }
  .tabla1 {
    background-color: $blue-tabla2;
    padding: .25rem 1.25rem;
  }
  .tabla2 {
    background-color: $blue-tabla4;
  }
}
.section-2 {
  padding-top: 3rem;
  padding-bottom: 3rem;
  background-color: $gray-claro;
  a {
    color: $gray-siroco;
    text-decoration: none;
    word-break: break-all;
    transition: all 0.3s;
    span {
      font-weight: 800;
    }
    &:hover {
      color: $blue-siroco;
    }
  }
  li {
    color: $gray-siroco;
    span {
      font-weight: 800;
    }
  }
}
